<template>
  <div id="tail">
      <p class="today">{{today}}</p>
      <p class="todayweek">星期{{todayweek}}</p>
  </div>
</template>

<script>
import bus from "../assets/eventbus.js";
export default {
  name: "tail",
  data() {
    return {
      today: 0,
      todayweek: 0
    };
  },
  mounted() {
    this.onload();
    this.re();
  },
  methods: {
    re: function() {
      var t = this;
      bus.$on("todays", function(data) {
        t.today = data[0];
        t.todayweek=data[1];
        t.judgeweek();
      });
    },
    onload:function(){
      var today=new Date();
      this.today=today.getDate();
      this.todayweek=today.getDay();
      this.judgeweek();
    },
    judgeweek: function() {
      if (this.todayweek == 0) {
        this.todayweek = "日";
      } else if (this.todayweek == 1) {
        this.todayweek = "一";
      } else if (this.todayweek == 2) {
        this.todayweek = "二";
      } else if (this.todayweek == 3) {
        this.todayweek = "三";
      } else if (this.todayweek == 4) {
        this.todayweek = "四";
      } else if (this.todayweek == 5) {
        this.todayweek = "五";
      } else if (this.todayweek == 6) {
        this.todayweek = "六";
      }
    }
  }
};
</script>

<style>
#tail{
    background:rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
    color:white;
}
.today{
    font-size: 40px;
    display: flex;
    align-items: center;
}
.todayweek{
    font-size: 40px;
    display: flex;
    align-items: center;
}
</style>



