<template>
  <div id="themain">
      <img src="../assets/left.png" class="minus" width="40px" height="40px" @click="minus">
            <img src="../assets/right.png" class="add" width="40px" height="40px" @click="add">
            <v-touch v-on:swipeleft="add" v-on:swiperight="minus" class="next">
            <ul>
                <li style="color:rgb(255, 0, 0)">日</li>
                <li style="color:rgb(9, 255, 0)">一</li>
                <li style="color:rgb(0, 195, 255)">二</li>
                <li style="color:rgb(255, 74, 201)">三</li>
                <li style="color:rgb(252, 248, 35)">四</li>
                <li style="color:rgb(98, 0, 255)">五</li>
                <li style="color:rgb(255, 255, 255)">六</li>
                <li class="showdays" v-for="(everyday,index) in days" @click="todays($event,index)">{{everyday.day}}</li>
            </ul>
            </v-touch>
  </div>
</template>

<script>
import bus from '../assets/eventbus.js';
export default {
  name: "themain",
  data() {
    return {
      thismonth: 0,
      thisyear: 0,
      thisday: 1,
      thisweek: 0,
      days: [],
      today: 0,
      todayweek: 0,
      todaymonth: 0,
      everyday: 1
    };
  },
  mounted(){
    this.rtday();
  },
  created() {
    this.firstonload();
    this.onload();
  },
  methods: {
    firstonload:function(){
      var today = new Date();
      this.thisday=today.getDate();
      today.setDate(1);
      this.thismonth = today.getMonth()+1;
      this.thisyear = today.getFullYear();
      this.thisweek = today.getDay();
    },
    onload: function() {
      var lastmonth = this.judgedays(this.thismonth - 1);
      var thismonth = this.judgedays(this.thismonth);
      var nextmonth = 35 + 1 - this.thisweek - thismonth;
      var i = 0;
      for (i = lastmonth - this.thisweek + 1; i <= lastmonth; i++) {
        let dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
        dayobject.day = i;
        this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用
      }
      if (thismonth + this.thisweek - 1 < 35) {
        for (i = 1; i <= thismonth; i++) {
          let dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
          dayobject.day = i;
          this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用
        }
      } else {
        for (i = 1; i < 36 - this.thisweek; i++) {
          let dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
          dayobject.day = i;
          this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用
        }
      }

      for (i = 1; i < nextmonth; i++) {
        let dayobject = {}; //用一个对象包装Date对象  以便为以后预定功能添加属性
        dayobject.day = i;
        this.days.push(dayobject); //将日期放入data 中的days数组 供页面渲染使用
      }
    },
    judgedays: function(changemonth) {
      if (changemonth == 13) {
        changemonth = 1;
      }
      if (changemonth == 0) {
        changemonth = 12;
      }
      if (
        changemonth == 1 ||
        changemonth == 3 ||
        changemonth == 5 ||
        changemonth == 7 ||
        changemonth == 8 ||
        changemonth == 10 ||
        changemonth == 12
      ) {
        return 31;
      } else if (changemonth == 2) {
        return 28;
      } else {
        return 30;
      }
    },
    minus: function() {
      this.days=[];
      if (this.thismonth == 1) {
        this.thismonth = 12;
      } else {
        this.thismonth--;
      }
      var newday = new Date();
      newday.setMonth(this.thismonth - 1);
      newday.setDate(1);
      this.thisweek = newday.getDay();
      this.onload();
      this.emittoday();
    },
    add: function() {
      this.days=[];
      if (this.thismonth == 12) {
        this.thismonth = 1;
      } else {
        this.thismonth++;
      }
      var newday = new Date();
      newday.setMonth(this.thismonth - 1);
      newday.setDate(1);
      this.thisweek = newday.getDay();
      this.onload();
      this.emittoday();
    },
    emittoday:function(){
      bus.$emit("ee",this.thismonth);
    },
    todays:function(e,d){
      var data=[];
      var el = e.currentTarget;
      data.push(el.innerHTML);
      var today=new Date();
      if(d<7&&el.innerHTML>20){
        today.setMonth(this.thismonth-2);
      }
      else if(d>20&&el.innerHTML<10){
        today.setMonth(this.thismonth);
      }
      else{
        today.setMonth(this.thismonth-1);
      }
      today.setDate(el.innerHTML);
      var l=today.getDay();
      data.push(l);
      bus.$emit("todays",data);
    },
    rtday:function(){
      var t=this;
      bus.$on("returnday",function(){
        t.days=[];
        var today=new Date();
        today.setDate(1);
        t.thismonth=today.getMonth()+1;
        t.thisweek=today.getDay();
        t.onload();
      })
    }
  }
};
</script>

<style>
#themain {
  width: 100%;
  height: 65%;
  background: rgb(0, 89, 255);
  color: black;
}
ul {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}
li {
  width: 14.1%;
  text-align: center;
  height: 7%;
  font-size: 1.2rem;
}
.add {
  position: absolute;
  right: 0;
}
.next{
  height: 100%;
  display: flex;
  justify-content: center;
}
</style>




