<template>
  <div id="app">
    <newheader></newheader>
    <themain id="themain"></themain>
    <tail></tail>
  </div>
</template>

<script>
import newheader from './components/header'
import themain  from './components/themain'
import tail from './components/tail'
export default {
  components:{
      newheader,
      themain,
      tail,
  },
  name: 'App'
}
</script>

<style>
#app{
  background:black;
  background-size: 100%;
  color:white;
  height: 100%;
}

</style>



