<template>
      <div id="header">
            <div id="returnthisday">
                <p @click="onload">今天</p>
            </div>
            <div id="left">
                <p id="thismonth">{{thismonth}}</p>
                <p class="year">{{thisyear}}</p>
            </div>
            <hr>
      </div>
</template>

<script>
  import bus from '../assets/eventbus.js'
  export default {
    name:'newheader',
    data(){
      return{
        thismonth: 0 ,
        thisyear: 0,
      }
    },
    created(){
      this.onload();
    },
    mounted(){
      this.re();
    },
    methods:{
      onload:function(){
        var today=new Date();
        this.thismonth=today.getMonth()+1;
        this.thisyear=today.getFullYear();
        bus.$emit("returnday");
      },
      re:function(){
        var t=this;
        bus.$on("ee",function(data){
          t.thismonth=data;
        });
      },
      test:function(){
        alert(123);
      }
    },
  };
</script>

<style>
*{
    margin:0px;
    padding:0px;
    list-style: none;
    border: none;
}
hr{
    border-top: 0.5px solid rgb(155, 154, 154);
    margin-bottom: 5px;
}
#header{
    background:rgb(37, 255, 255);
    height: 12%;
    display: flex;
    align-items: center;
    color: black;
}
#thismonth,.year{
    display: flex;
    flex-direction: row;
}
#thismonth{
    font-size: 30px;
}
#returnthisday{
    position: absolute;
    right: 0;
}
#left{
    display: flex;
    flex-direction: column;
}
#returnthisday{
    float: right;
}
</style>



